<template>
  <div :ref="name" :class="name">
    <div class="content">
      <slot></slot>
    </div>
  </div>
</template>

<script>
import BScroll from 'better-scroll';
import { setTimeout } from 'timers';
export default {
  name: 'ComScroll',
  props: {
    name: String,
    scrollX: {
      type: Boolean,
      default :false
    },
    scrollY: {
      type: Boolean,
      default :false
    },
    click: {
      type: Boolean,
      default: false
    },
    pullDownRefresh: {
      type: null,
      default: false
    },
    pullUpLoad: {
      type: null,
      default: false
    },
    myPullDownRefresh: Function,
    myPullUpLoad: Function,
    // myScrollTo: Function
  },
  methods: {
    initScroll () {
      this.scroll = new BScroll(this.$refs[this.name], {
        scrollX: this.scrollX,
        scrollY: this.scrollY,
        click: this.click,
        pullDownRefresh: this.pullDownRefresh,
        pullUpLoad: this.pullUpLoad
      })
      if (this.pullDownRefresh) {
        this.scroll.on("pullingDown", () => {
          this.myPullDownRefresh();
          this.scroll.finishPullDown();
        })
      }
      if (this.pullUpLoad) {
        this.scroll.on("pullingUp", () => {
          this.myPullUpLoad();
          this.scroll.finishPullUp();
        })
      }
    }
  },
  mounted () {
    setTimeout(() => {
      this.initScroll();
    }, 20)
  }
}
</script>


