<template>
  <ComScroll v-bind="options">
    <ul>
      <MovieItem1 :movieList="movieList" />
    </ul>
  </ComScroll>
</template>

<script>
import axios from 'axios';
import BScroll from 'better-scroll';
import MovieItem1 from './common/MovieItem1.vue';
import ComScroll from '../components/common/ComScroll.vue'
export default {
  name: 'HotMovies',
  components: {
    MovieItem1,
    ComScroll
  },
  data () {
    return {
      //滑动组件的参数
      options: {
        name: 'hotMovie',
        scrollY: true,
        click: true,
        pullDownRefresh: {
          threshold: 50,
          stop: 0
        },
        pullUpLoad: {
          threshold: -50
        },
        myPullDownRefresh: this.myPullDownRefresh,
        myPullUpLoad: this.myPullUpLoad,
      },
      movieList: [],
      windowHeight: (window.innerHeight -140) + 'px'
    }
  },
  methods: {
    myPullDownRefresh () {
      console.log('更新了')
    },
    myPullUpLoad () {
      console.log('增加了')
    }
  },
  created () {
    axios.post('/movieOnInfoList')
      .then((res) => {
        this.movieList = res.data;
      })
  },
  mounted () {
    document.getElementsByClassName('hotMovie')[0].style.height = this.windowHeight;
    //监听窗口改变，修改滑动块的参数
    window.onresize = () => {
      this.windowHeight = (window.innerHeight - 140) + 'px';
      document.getElementsByClassName('hotMovie')[0].style.height = this.windowHeight;
    }
  }
}
</script>


<style lang='scss' scoped>
.hotMovie {
  width: 100%;
  overflow: hidden;
  transform: translateY(90px)
}
</style>